<template>
	<div style="margin: 20px;width: 600px;">
		<el-upload
		  class="upload-demo"
		  drag
		  action=""
		  :on-change="handleChange"
  		  :on-remove="handleRemove"
  		  :file-list="fileList"
  		  :auto-upload="false"
  		  list-type="picture"
		  multiple>
		  <i class="el-icon-upload"></i>
		  <div class="el-upload__text"><em></em></div>
		  <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
		</el-upload>
		<div class="content-title" style="margin-top: 100px;font-size: 24px;"></div>
        <div class="plugins-tips">
            Vue-Core-Image-Uploadvue
            <a href="https://github.com/Vanthink-UED/vue-core-image-upload" target="_blank">Vue-Core-Image-Upload</a>
        </div>
        <img class="pre-img" :src="src" alt="" style="margin:50px 0;">
        <vue-core-image-upload 
	       crop="local"
	       text=""
	       url=""
	       extensions="png,gif,jpeg,jpg"
	       @:imageuploaded="imageuploaded"
	       @imagechanged="imagechanged"
	       @:errorhandle="handleError"></vue-core-image-upload>
	</div>
</template>
<script>
	import VueCoreImageUpload  from 'vue-core-image-upload';
	var _img = require('../../../images/touxiang.jpg');
	export default {
		data() {
			return {
				fileList: [],
				src: _img
			}
		},
		components: {
			'vue-core-image-upload': VueCoreImageUpload
		},
		methods: {
			handleChange: function(file, fileList){
				console.log(fileList);
				//this.fileList.push(file);
			},
			handleRemove: function(file, fileList){
				console.log(file, fileList);
			},
			handleError: function(){
                this.$notify.error({
                    title: '',
                    message: ''
                });
            },
            imageuploaded: function(res){
            	console.log(res);
            },
            imagechanged: function(){
            	console.log('change');
            }
		}
	}
</script>


